@font-face {
  font-family: dual;
  src: url(dual.woff);
}

body {
  background-color: #2D3B62;
}

* {
  margin: 0;
  box-sizing: border-box;
}

.star-header {
  /* background-color: #f00; */
  /* height: 20vw; */
  display: flex;
  flex-direction: column;
  position: fixed;
}


.star-chart-container {
  /* position: fixed; */
  height: 20vw;
  min-height: 5vw;
  width: 100vw;
  background-color: #2D3B62;
  background: linear-gradient(0deg, #2D3B62FF, #29375EFF);
  overflow: hidden;
  
	opacity: 0;  /* make things invisible upon start */
	-webkit-animation: fadeIn ease-in 1;  /* call our keyframe named fadeIn, use animattion ease-in and repeat it only 1 time */
	-moz-animation: fadeIn ease-in 1;
	animation: fadeIn ease-in 1;

	-webkit-animation-fill-mode: forwards;  /* this makes sure that after animation is done we remain at the last keyframe value (opacity: 1)*/
	-moz-animation-fill-mode: forwards;
	animation-fill-mode: forwards;

	-webkit-animation-duration: 2s;
	-moz-animation-duration: 2s;
	animation-duration: 2s;
  
  -webkit-animation-delay: 0.2s;
  -moz-animation-delay: 0.2s;
  animation-delay: 0.2s;
}

.star-chart {
  height: 250vw;
  pointer-events: none;
  margin: -45vw 0 0 -70vw;
  opacity: 0.7;
  filter: drop-shadow(0 0 5px #FFD3);  /* blur on stars themselves drop-shadow(offset-x offset-y blur-radius color) */
  -webkit-animation: starSpin infinite 600s linear;
  -moz-animation: starSpin infinite 600s linear;
  animation: starSpin infinite 600s linear;
}

.zodiac-logotype-container {
  /* margin: auto; */
  text-transform: uppercase;
  text-shadow: 0 0 8px #2D3B62FF;
  color: #ffd;
  opacity: 0;  /* make things invisible upon start */
	-webkit-animation: fadeIn ease-in 1;
	-moz-animation: fadeIn ease-in 1;
	animation: fadeIn ease-in 1;

	-webkit-animation-fill-mode: forwards;
	-moz-animation-fill-mode: forwards;
	animation-fill-mode: forwards;

	-webkit-animation-duration: 1s;
	-moz-animation-duration: 1s;
	animation-duration: 1s;
  
  -webkit-animation-delay: 0.6s;
  -moz-animation-delay: 0.6s;
  animation-delay: 0.6s;
}

.zodiac-logotype-main {
  /* margin: auto; */
  /* background-color: #0000; */
  /* position: absolute; */
  margin-top: -16vw;
  margin-left: 16vw;
  font: 14vw dual, "Dual", "Futura", sans-serif;
  letter-spacing: 0.2em;
  /* text-align: center; */
  
  animation: shrinkLogotype ease-in-out 1;
	animation-fill-mode: forwards;
	animation-duration: 1s;
  animation-delay: 2s;
}

@keyframes shrinkLogotype { 
  from { 
    font-size: 14vw; 
    margin-top: -16vw;
    margin-left: 16vw;
    /* text-align: center; */
  } 
  to { 
    font-size: 3vw;
    margin-top: -19vw;
    margin-left: 1vw;
    /* text-align: left; */
  } 
}

