/* Fade in animation */
@-webkit-keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }
@-moz-keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }
@keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }

/* Rotation animation for the stars */
@-webkit-keyframes starSpin { from { transform: rotate(0deg); } to { transform: rotate(360deg); } }
@-moz-keyframes starSpin { from { transform: rotate(0deg); } to { transform: rotate(360deg); } }
@keyframes starSpin { from { transform: rotate(0deg); } to { transform: rotate(360deg); } }






